<template>
  <div>
    <!-- 顶部logo 和 逛一逛 -->
    <div class="Login_title">
      <van-nav-bar>
        <!-- 左边 -->
        <template #left>
          <div class="title_logo">
            <img src="../assets/images/home_active.png" alt="" />
          </div>
          <div class="title_logo_wenzi">Luckin Coffee</div>
        </template>
        <!-- 右边 -->
        <template v-slot:right>
          <div class="title_right_btn">先逛一逛</div>
        </template>
      </van-nav-bar>
    </div>

    <!-- 页面内容 -->
    <div class="content">
      <!-- 界面文字部分 -->
      <div class="content_box">
        <div class="content_chinese">找回密码!</div>
        <div class="content_EngList">FindPassword</div>
      </div>
      <!-- 表单位置 -->
      <div class="content_table">
        <van-form>
          <van-field
            v-model="phoneNumber"
            type="text"
            name="用户名"
            label="手机号"
            placeholder="手机号"
          />
          <van-field
            v-model="password"
            name="用户名"
            type="password"
            label="密码"
            placeholder="密码"
          />

          <div class="content_login"><span @click=" goLogin()">已有账号? 登录一下吧</span></div>
          <div style="margin: 30px 15px">
            <van-button
              round
              block
              color="blue"
              type="info"
              native-type="submit"
              @click="retrievePassword()"
              >提交</van-button
            >
          </div>
          <div style="margin: 30px 15px">
            <van-button @click="goUpPage()" round block plain type="info" native-type="submit"
              >返回</van-button
            >
          </div>
        </van-form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "FindPasswordnext",
  data() {
    return {
      // 手机号
      phoneNumber: "",

      // 密码
      password: "",
    };
  },
  methods: {
      // 修改密码
    retrievePassword() {
        this.axios({
            method:"POST",
            url:"/retrievePassword",
            data:{
                phone:this.phoneNumber,
                password:this.password
            }
        }).then(res =>{
            this.$toast(res.data.msg)
            if(res.data.code === "L001"){
                setTimeout(()=>{
                    this.$router.push({name:"Login"})
                })
            }
            
        }).catch(err =>{
            
        })
    },
    // 返回
    goUpPage(){
        this.$router.go(-1)
    },
    // 跳转到登录页面
    goLogin(){
        this.$router.push({name:"Login"})
    }
  },
};
</script>

<style lang="less" scoped>
// 顶部logo 和 逛一逛 样式
.title_logo {
  width: 45px;
  height: 45px;
  img {
    width: 100%;
    height: 100%;
  }
}
.title_logo_wenzi {
  font-size: 18px;
  color: #333;
}
.title_right_btn {
  color: blue;
  font-weight: bold;
  font-size: 13px;
}

// 界面文字部分
.content {
  position: fixed;
  top: 50px;
  bottom: 0;
  left: 0;
  right: 0;
  padding-top: 100px;

  // 界面文字部分
  .content_box {
    padding-left: 20px;
    .content_chinese {
      font-size: 30px;
      font-weight: bolder;
      color: #555;
      margin-bottom: 20px;
    }
    .content_EngList {
      font-size: 14px;
      color: #999;
    }
  }
  .content_table {
    margin-top: 30px;
    .content_login {
      padding-top: 15px;
      text-align: right;
      padding-right: 20px;
      span {
        padding: 5px 10px;
      }
    }
  }
}
</style>